$(".tabs-1 .tabs > li").bind("click", function () {
   var $this = $(this);
   $(".tabs-1 .tabs > li").removeClass("active");
   $this.addClass("active");
   $(".tabs-1 .tabs-content > li").removeClass("active");
   $(".tabs-1 .tabs-content > li")
      .eq($this.index())
      .addClass("active");
});